import React, { Component } from "react";
import Link from "gatsby-link";
import SkillsList from '../../components/SkillsList';

class DocsSkills extends Component {
    constructor(props) {
        super(props);
        const data = this.props.data;
        this.state = {
            initialData: {...data},
            data: {}
        };
        console.log(data);
    }

    componentWillMount() {
        this.setState({data: {...this.state.initialData}});
    }

    handleFilterList(event) {
        let updatedList = {...this.state.initialData};
        updatedList.allMarkdownRemark.edges = updatedList.allMarkdownRemark.edges.filter(item => {
            return item.node.frontmatter.title.toLowerCase().search(
                event.target.value.toLowerCase()) !== -1;
        });
        this.setState({data: updatedList});
    }

    render() {
        return (
            <div>
                <div className="container-fluid">
                    <div className="sidenav-doc">
                        <a href="/docs/">Policy</a>
                        <Link to="/docs/skills">Skills</Link>
                        <Link to="/docs/spells">Spells</Link>
                        <Link to="/docs/powers">Powers</Link>
                        <a href="#section3"></a>
                    </div>
                </div>
                <div className="document">
                    <h1 className="text-center">Skills</h1>
                    <div className="col-lg-12">
                        <div className="filter-list">
                            <form>
                                <fieldset className="form-group">
                                    {/* <input type="text" className="form-control form-control-lg" placeholder="Search..." onChange={(e) => this.handleFilterList(e)}/> */}
                                </fieldset>
                            </form>
                        </div>
                    </div>
                    <SkillsList skills={this.state.data} />
                </div>    
            </div>
        );
    }
}



export default DocsSkills;
    

export const query = graphql `
query SkillsList {
    allMarkdownRemark (
        sort: {fields: [frontmatter___title], order: ASC},
        filter: {fileAbsolutePath: {regex: "/(docs)/(skills)/.*\\.md$/"}}
    ) {
        edges {
            node {
                id
                html
                frontmatter {
                    title
                    syntax
                    group
                    level
                    offensive
                }
            }
        }
    }
}
`


